<template>
    <yd-layout>
        <yd-navbar title="会议签到" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="#" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="mainContentFluid thirdPart">
            <div class="fluidWidth">
                <yd-flexbox direction="vertical">
                    <div>签到码：向参会者展示此二维码</div>
                    <div>提前30分钟扫码签到</div>
                    <div class="starTime" v-text="'开始时间：'+time"></div>
                </yd-flexbox>
                <div class="Qr_code"><img :src="conf.qrCodeURL+imgURL" /></div>
            </div>
            <div class="save" @click="isPhone()"><a :href="conf.qrCodeURL+imgURL" download>保存二维码</a></div>
        </div>
        <div class="bottom" v-text="'共'+Pp+'人参加，'+conPp+'人签到'"></div>
    </yd-layout>
</template>
<script>
export default {
    data() {
        return {
            time: '',
            imgURL: '',
            title: '',
            conPp: '',
            Pp: ''
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('会议签到');
        vm.time = vm.$route.params.time.substr(10);
        vm.imgURL = vm.$route.params.codeImg;
        vm.title = vm.$route.params.title;
        vm.conPp = vm.$route.params.conPp;
        vm.Pp = vm.$route.params.Pp;
        console.log(vm.conf.qrCodeURL + vm.imgURL);
    },
    methods: {
        isPhone: function () {
            let vm = this;
            if (vm.isIOS) {
                vm.$dialog.toast({
                    mes: '苹果手机请长按保存图片',
                    timeout: 1500
                });
            }
        }
    }
}
</script>
<style scoped>
.starTime {
    color: #999;
}

.Qr_code {
    text-align: center;
    margin-top: 0.3rem;
}

.thirdPart {
    margin-top: 0;
    padding-bottom: 3rem;
}

.save {
    width: 4.54rem;
    height: 0.66rem;
    text-align: center;
    line-height: 0.66rem;
    background-color: #61b3f6;
    color: #fff;
    font-size: 0.3rem;
    margin: 0 auto;
    border-radius: 0.1rem;
    margin-top: 0.63rem;
}

.bottom {
    position: fixed;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 0.8rem;
    line-height: 0.8rem;
    text-align: center;
    font-size: 0.3rem;
    color: #999;
    background-color: #fff;
}
</style>


